// Home.js
import React from 'react';

const Regles = () => {
  return (
    <div className="regles">
  <h1>Rgles officielles du coin-coin</h1>
    <h2>1. Matriel</h2>
    <p>Pour jouer au coin-coin, il vous faut :<br />
        1 table (grande ou petite, certaines rgles changent en fonction de la taille de la table)<br />
        2 quipes de 2 joueurs<br />
        2 verres de 25cl par personne, 4 verres en tout, remplis de bire, ou de limonade pour le soft(les bulles sont importantes)<br />
        1 balle de ping-pong<br />
        2 verres dans chaque coin de la table<br />
    </p>
    <h2>2. But du jeu</h2>
    <p>Le but du jeu est de boire tous ses verres avant l'quipe adverse.
    Chacun son tour les quipes vont lancer la balle de ping-pong, pour essayer de toucher un verre en face pour permettre  son duo de boire.

    </p>
    <h2>3. Droulement d'une manche</h2>
    <p>Positionnement: <br />
    Equipe tireuse: <br />
    - le joueur tireur se place derrire sa table, le coude (la main pour les petites tables, au choix) ne doit jamais passer au dessus de la table pendant le tir.<br />
    - le joueur buveur peut tenir son verre  la main, sans le pencher, toujours pos entirement sur la table. Il peut s'agenouiller, tenir le verre, poser les lvres sur le verre.<br />
    Equipe ramasseuse: <br />
    - les joueurs ramasseurs se placent derrire leur table, prts  rattraper, au moins une main qui touche la table, jamais devant le verre. <br />   
    </p>
    <p>Lancer de balle: <br />
    Si la balle touche le verre, rentre dans le verre, rebondit sur un rattrapeur et touche le verre, rebondit sur la table puis touche le verre, le tir est un succs.
    Si la balle fait tomber le verre, le tir est un succs, mais le verre tomb est considr bu. Si c'est qqc d'autre qui fait tomber le verre, on le remplit  peu prs au niveau auquel il tait.
    </p>
    </div>
)
  ;

};

export default Regles;
